import { useState, useCallback } from 'react';
import { ChatState, ChatMessage, ToolUsage } from '../types';

const generateId = () => Math.random().toString(36).substr(2, 9);

export const useChatState = (initialPosition = { x: window.innerWidth - 370, y: 50 }) => {
  const [chatState, setChatState] = useState<ChatState>({
    messages: [],
    isMinimized: false,
    position: initialPosition,
    isVisible: false,
  });
  const [isLoading, setIsLoading] = useState(false);

  const addMessage = useCallback((
    content: string, 
    sender: 'user' | 'ai', 
    toolsUsed?: ToolUsage[], 
    toolUsageIndicators?: ToolUsage[],
    hasToolUsage?: boolean
  ) => {
    const newMessage: ChatMessage = {
      id: generateId(),
      content,
      sender,
      timestamp: new Date(),
      toolsUsed,
      toolUsageIndicators,
      hasToolUsage,
    };

    setChatState(prev => ({
      ...prev,
      messages: [...prev.messages, newMessage],
    }));
  }, []);

  const clearMessages = useCallback(() => {
    setChatState(prev => ({
      ...prev,
      messages: [],
    }));
  }, []);

  const toggleVisibility = useCallback(() => {
    setChatState(prev => ({
      ...prev,
      isVisible: !prev.isVisible,
    }));
  }, []);

  const toggleMinimize = useCallback(() => {
    setChatState(prev => ({
      ...prev,
      isMinimized: !prev.isMinimized,
    }));
  }, []);

  const updatePosition = useCallback((position: { x: number; y: number }) => {
    setChatState(prev => ({
      ...prev,
      position,
    }));
  }, []);

  const sendMessage = useCallback(async (message: string, paneContent?: Record<string, string>) => {
    addMessage(message, 'user');
    setIsLoading(true);
    
    try {
      // Prepare conversation history for the API
      const conversationHistory = chatState.messages.map(msg => ({
        role: msg.sender === 'ai' ? 'assistant' : msg.sender,
        content: msg.content,
        timestamp: msg.timestamp.toISOString()
      }));
      
      // Call the backend API
      const response = await fetch('http://localhost:8000/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: message,
          conversation_history: conversationHistory,
          pane_content: paneContent
        })
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      // Debug logging
      console.log('Backend response:', data);
      console.log('Tools used:', data.tools_used);
      console.log('Tool usage indicators:', data.tool_usage_indicators);
      console.log('Has tool usage:', data.has_tool_usage);
      
      if (data.success) {
        addMessage(
          data.response, 
          'ai', 
          data.tools_used || [], 
          data.tool_usage_indicators || [],
          data.has_tool_usage || false
        );
      } else {
        addMessage(`Error: ${data.error || 'Failed to get response from AI'}`, 'ai');
      }
    } catch (error) {
      console.error('Error sending message:', error);
      addMessage(`Error: ${error instanceof Error ? error.message : 'Failed to connect to AI service'}`, 'ai');
    } finally {
      setIsLoading(false);
    }
  }, [addMessage, chatState.messages]);

  return {
    chatState,
    isLoading,
    addMessage,
    clearMessages,
    toggleVisibility,
    toggleMinimize,
    updatePosition,
    sendMessage,
  };
};
